@import "vars";
@import "mixins";

.page-header {
  padding-top: 10px;
  padding-bottom: 18px;
}

.page-header__wrapper {
  .wrapper;
}

.page-header__list {
  list-style: none;
  margin: 0;
  padding: 0;
  min-height: 38px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.page-header__logo-img,
.page-header__name-img {
  display: block;
}

